<template>
	<view class="contrain">
		<view class="person-item" v-for="(item,index) in violates" :key="index">
			<view class="cname">{{item.companyName}}</view>
			<view class="rcz">{{item.illegalMsg}}</view>
			<view class="cztime">{{item.times}}</view>
		</view>
	</view>
</template>

<script>
	import date from '../../utils/date.js';
	export default {
		data() {
			return {
				pageNum: 1,
				pages: 1,
				violates: [],
			}
		},
		onLoad() {
			this.getViolates()
		},
		methods: {
			getViolates() {
				let userInfo = uni.getStorageSync('userInfo')
				let api = `/message/illegal/${userInfo.identityType}`;
				let data = {
					userId: userInfo.userId,
					pageNum: this.pageNum,
					pageSize: 10
				};
				this.http(api, "GET", data).then(res => {
					console.log(res)
					uni.stopPullDownRefresh()//停止动画
					if (res.code == 200) {
						res.data.rows.forEach((item, i) => {
							let tiems = date.toDate(item.createTime)
							tiems = tiems.split(' ');
							let leftTime = tiems[0].split('-')[1] + '-' + tiems[0].split('-')[2]
							let rightTime = tiems[1].split(':')[0] + ':' + tiems[1].split(':')[1]
							item.times = leftTime + " " + rightTime
						})
						this.violates = this.violates.concat(res.data.rows);
						this.pages = res.data.pages;
					}
				})
			},
		},
		// 监听用户上拉加载
		onReachBottom(){
			if (this.pages > this.pageNum) {
				this.pageNum += 1
				this.getViolates();
			}
		},
		// 监听用户下拉刷新
		onPullDownRefresh(){
			this.pageNum = 1;
			this.violates = [];
			this.getViolates();
		},
	}
</script>

<style scoped>
	.person-item {
		padding: 12rpx 14rpx 42rpx;
		box-sizing: border-box;
	}

	.cname {
		width: 100%;
		height: 56rpx;
		line-height: 56rpx;
		padding-left: 13rpx;
		box-sizing: border-box;
		color: #fff;
		font-size: 28rpx;
		background: #014B84;
	}

	.rcz {
		font-size: 24rpx;
		margin-left: 13rpx;
		margin-top: 23rpx;
	}

	.cztime {
		font-size: 24rpx;
		margin-left: 13rpx;
		margin-top: 28rpx;
	}
</style>
